<template lang="pug">
  div(id="game-root")
    user-panel(
      v-if="!isOnBuildMode"
      :user="user"
      :planet="planet"
      :mode="'Game'"
      @openModal="openModal")
    feature-menu(v-if="!isOnBuildMode && !isSelectBuilding" class="feature-menu" @openModal="openModal" :isModalOpen="!isModalOpen")
    building-info(v-else-if="isSelectBuilding" :building="selectedBuilding" @openModal="openModal" @close="close")
    transition(name="modal")
      modal-card(v-if="isModalOpen" @close="close")
        user-profile(v-if="currentOpen === 'user-profile'" slot="card" :user="user")
        building-upgrade(v-if="currentOpen === '建築升級'" slot="card" :building="selectedBuilding")
        building-feature(v-if="currentOpen === '建築功能'" slot="card" :building="selectedBuilding" :planet="planet" @nextScenes="nextScenes")
        building(v-if="currentOpen === '建造'" slot="card" :user="user" @onBuildMode="onBuildMode")
        warehouse(v-if="currentOpen === '倉庫'" slot="card" :user="user")
        mission(v-if="currentOpen === '任務'" slot="card" :user="user" :planet='planet')
    build-mode(v-if="isOnBuildMode" @build="build" :building="selectedBuilding")
    Story(
      v-if="actions[currentScenes] === '故事'"
      :currentScenes="currentScenes"
      @nextScenes="nextScenes")
    Dialog(
      v-else
      :currentScenes="currentScenes"
      @nextScenes="nextScenes")
</template>

<script>
import UserPanel from '@/components/user/UserPanel.vue'
import FeatureMenu from '@/components/game/FeatureMenu.vue'
import ModalCard from '@/components/ModalCard.vue'
import UserProfile from '@/components/user/UserProfile.vue'
import Building from '@/components/game/build/Building.vue'
import BuildMode from '@/components/game/build/BuildMode.vue'
import BuildingInfo from '@/components/game/build/BuildingInfo.vue'
import BuildingUpgrade from '@/components/game/build/BuildingUpgrade'
import Warehouse from '@/components/game/warehouse/Warehouse.vue'
import Mission from '@/components/game/mission/Mission.vue'
import BuildingFeature from '@/components/game/build/buildingFeature/BuildingFeature.vue'
import Dialog from '@/components/beginnerGuide/Dialog.vue'
import Story from '@/components/beginnerGuide/Story.vue'

import { mapState } from 'vuex'

export default {
  name: 'Game',
  components: {
    FeatureMenu,
    UserPanel,
    ModalCard,
    UserProfile,
    Building,
    BuildMode,
    BuildingInfo,
    BuildingUpgrade,
    Warehouse,
    Mission,
    BuildingFeature,
    Dialog,
    Story
  },
  data () {
    return {
      selectedId: '',
      isModalOpen: false,
      currentOpen: '',
      downloaded: false,
      isOnBuildMode: false,
      module: null,
      isSelectBuilding: false,
      selectedBuilding: '',
      currentScenes: 0,
      actions: [
        'dialog',
        '建造',
        'dialog',
        '建造該建築',
        '建造下去',
        'done',
        '風車',
        '建築功能',
        'dialog',
        '資源傳輸中心',
        '進行研發',
        '離開',
        '建造',
        '建造該建築',
        '建造下去',
        '故事',
        '故事',
        '故事',
        '故事',
        '採集'
      ]
    }
  },
  mounted () {
    this.$store.watch((state, getters) => {
      return getters
    }, (newValue, oldValue) => {
      this.isSelectBuilding = newValue['planet/getIsSelectBuilding']
      this.selectedBuilding = newValue['planet/getBuildElement']
      if (this.actions[this.currentScenes] === this.selectedBuilding.status) {
        this.currentScenes += 1
        return
      }
      if (this.actions[this.currentScenes] === this.selectedBuilding.name_zh) {
        this.currentScenes += 1
      }
    }, { deep: true }
    )
  },
  created () {
    import('@/game/scenes/PlayScene').then(async (module) => {
      this.module = module
    })
  },
  methods: {
    nextScenes (action) {
      console.log(action)
      if (this.actions[this.currentScenes] === action) {
        this.currentScenes += 1
        return true
      }
      return false
    },
    openModal (one) {
      if (this.nextScenes(one)) {
        this.isModalOpen = true
        this.isOnBuildMode = false
        this.currentOpen = one
      }
    },
    build () {
      this.currentScenes += 1
      this.isModalOpen = false
      this.isOnBuildMode = false
      this.$store.commit('planet/SET_ISSELECTBUILDING', false)
    },
    close () {
      if (this.nextScenes('離開')) {
        this.isModalOpen = false
        this.isOnBuildMode = false
        this.$store.commit('planet/SET_ISSELECTBUILDING', false)
      }
    },
    onBuildMode (building) {
      if (this.nextScenes('建造該建築')) {
        this.isModalOpen = false
        this.isOnBuildMode = true
        this.selectedBuilding = building
      }
    }
  },
  watch: {
    isModalOpen: function () {
      this.module.gameFunction.scene.input.enabled = !this.isModalOpen
    }
  },
  computed: {
    ...mapState('user', {
      user: state => state
    }),
    ...mapState('planet', {
      planet: state => state.planet
    })
  }
}
</script>

<style lang="scss" scoped>
@import "@/assets/scss/variables.scss";

#game-root {
  #game-container {
    height: 100%;
    overflow-y: hidden;
  }
  .feature-menu {
    position: fixed;
    bottom: 20px;
  }
}
.modal-enter-active {
  transition: all 1s 0.5s;
}
.modal-leave-active {
  transition: all 0.5s;
}
.modal-enter,
.modal-leave-to {
  opacity: 0;
}
</style>
